import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle'; import ReactStars from 'react-stars'
import { useState } from 'react';


const CommentDialog = ({ open, handleClose, user, product }) => {
  const [isPending, setIsPending] = useState(false);
  const [isError, setIsError] = useState(false)
  const [comment, setComment] = useState({
    text: "",
    timestamp: (new Date()),
    username: user.username
  })
  const textChanged = (newText) => {
    let commentTemp = { ...comment }
    commentTemp.text = newText
    setComment(commentTemp)
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    console.log(comment)
    console.log(user)
    setIsPending(true)
    fetch('/data/products/comment/' + product._id, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify(comment)
    }).then(response =>
      response.json().then(data => ({
        data: data,
        status: response.status
      })
      ).then(res => {

        setIsError(res.data)
        setIsPending(false)
        handleClose()
      }));
  }
  return (
    <Dialog open={open} onClose={handleClose} aria-labelledby="form-dialog-title">
      <DialogTitle id="form-dialog-title">Comment</DialogTitle>
      <DialogContent>
        <DialogContentText>
          Your comment is extremely valuable for us. We will try to reply as fast as possible
        </DialogContentText>
        <TextField
          autoFocus
          disabled={isPending}
          margin="normal"
          id="comment"
          label="Your comment"
          type="text"
          onChange={(e) => {
            textChanged(e.target.value)
          }}
          multiline
          fullWidth
        />
      </DialogContent>
      <DialogActions>
        <button onClick={handleClose}>
          Cancel
        </button>
        <button disabled={isPending} onClick={handleSubmit}>
          Comment
        </button>


      </DialogActions>
    </Dialog>)

}
export default CommentDialog;